@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap");
.App {
  font-family: "Open Sans", sans-serif;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.pointer {
  cursor: pointer;
}

.negative-btn {
  background: #FFFFFF;
border: 1px solid #C3C3C3;
box-sizing: border-box;
border-radius: 6px;
color: #f17e33;
min-width: 98px;
font-weight: 600;
font-size: 14px;
line-height: 20px;
}

.positive-btn {
  font-weight: 600;
font-size: 14px;
line-height: 20px;
  background: #f17e33;
  border-radius: 6px;
  color: white;
  min-width: 98px;
}


.mandatory::after{
  content: "*";
color: red;
font-size: 14px;
margin-left: 5px;
}


.isSelected{
  background-color: rgba(254, 242, 235, 1) !important;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  margin-right: 5px;
  background: transparent; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #7d7d7d67; 
  border-radius: 4px;
  
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(125, 125, 125, 0.63); 
  opacity: 0.3;
}