}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  margin-right: 5px;
  background: transparent; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #7d7d7d67; 
  border-radius: 4px;
  
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(125, 125, 125, 0.63); 
  opacity: 0.3;
}
.cust-tile{
    display: flex;
    min-height: 72px;
    min-width: 320px;
    width: 100% !important;
    border-top: 1.5px solid rgba(87, 79, 79, 0.19);
    background: #FFFFFF;
    cursor: pointer;
    height: 10vh;
}

.first-tile{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(87, 79, 79, 0.19);
}

.tile-details{
    display: flex;
    padding-left: 30px;
    justify-content: start;
    align-items: center;
    width: 80%;
    background-color:"red";
}

.tile-details div{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.tile-header{
    margin: 0;
    font-weight: 600;
    font-size: 2vh;
    line-height: 1.5;
}

.tile-description{
    font-weight: normal;
    font-size: 1.5vh;
        font-weight: 400;
}

.tile-actions{
    width: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 400;
}

.first{
    border-top: 0px !important;
}
